<template>
  <div class="report">
    <section class="top">
      <hr>
      <div class="wb">
        <a href="javascript:;" class="active">发布图文评测</a> 
        <a href="javascript:;">发布视频评测</a> 
        <!-- <a href="javascript:;">上传服务协议</a> -->
      </div>
      <div class="subtext">
        <a href="javascript:;">上传服务协议</a>
      </div>
    </section>
    <!-- 协议内容 -->
    <section class="negotiate">
      <h2>发布图文评测</h2>
      <div class="con conone">
        <p>
          <span class="red">上传封面</span>
          推荐尺寸280*200像素，支持上传jpg/gif/png格式，且文件小于2M
        </p>
        <div class="paper">
          <div class="papercon">
            <img src="../../assets/images/reportimg.png" alt="">
            <p>点击上传封面，或将文件拖拽到此处</p>
          </div>
        </div>
      </div>
      <h3>评测信息</h3>
      <div class="con contwo">
        <p>
          <span class="red">图文标题</span>
          <input type="text" placeholder="请输入文章标题">
        </p>
        <p>
          <span class="red">标签</span>
          <input type="text" placeholder="您可添加3个标签，按回车键确认，描述越准确，越利于触达兴趣人群">
        </p>
        <p>
          <span class="red">标签</span>
          <select name="" id="">
            <option value="">请选择</option>
          </select>
        </p>
        <p>
          <span class="red">图文简介</span>
          <textarea name="" id="" placeholder="请输入简介"></textarea>
        </p>

      </div>
      <h3 class="red">评测内容</h3>
      <div class="imgrp">
        <img src="../../assets/images/rpbk.jpg" alt="">
      </div>
    </section>
    <div class="btn">
      <input class="active" type="submit" value="提交审核">
      <input type="submit" value="存草稿">
      <input type="submit" value="预览">
    </div>
  </div>
</template>

<script>
export default {
  name: 'pcreleasereport',
  data() {
    return {}
  }
}
</script>
<style lang='scss' scoped>
.report{
  width:1200px;
  margin: 0 auto;
  section.top{
    position: relative;
    height: 50px;
    margin:30px auto;
    hr{
      width:100%;
      position: absolute;
      top:38%;
      z-index: 1;
      height: 0;
      border-top:0px;
      border-bottom:1px solid #959595;
    }
    .wb{
      background: #f9f9f9;
      position: absolute;
      width:400px;
      text-align: center;
      z-index: 2;
      top:30%;
      left: 50%;
      transform: translate(-50%);
      a{
        width:170px;
        margin:0 10px;
        display: inline-block;
        font-size: 20px;
        color:#878787;
        &:hover{
          color: #212121;
        }
      }
      .active{
        color: #212121;
      }
    }
    .subtext{
      background: #f9f9f9;
      position: absolute;
      text-align: center;
      z-index: 2;
      top:35%;
      right:-2px;
      a{
        width:100px;
        display: inline-block;
        text-align: right;
        font-size: 14px;
        color: #989898;
      }
    }
  }
  section.negotiate{
    background: #fff;
    min-height: 1440px;
    h2{
      font-size:24px;
      height: 90px;
      line-height: 90px;
      color:#373737;
      font-weight: 400;
      padding-left:30px;
      border-bottom:4px solid #f1f1f1;
    }
    h3{
      font-size:18px;
      height: 60px;
      line-height: 60px;
      color: #373737;
      font-weight:400;
      margin:20px auto;
      padding-left: 60px;
      border-top:5px solid #f9f9f9;
      border-bottom:2px solid #f1f1f1;

    }
    .red{
      &:after{
        content: "*";
        color: red;
        margin:0 5px;
      }
    }
    .imgrp{
      width:90%;
      margin:0 auto;
      img{
        width:100%;
      }
    }
    .con{
      width:90%;
      margin:0 auto;
      p{
        font-size: 16px;
        color: #a7a7a7;
        margin:20px auto;
          span{
            height: 40px;
            line-height: 40px;
            font-size: 18px;
            width:100px;
            display: inline-block;
            color: #373737;
          }
          input{
            width:87%;
            height:40px;
            padding-left: 10px;
            border-radius: 5px;
            float: right;
            color:#a7a7a7;
            border:1px solid #DFE0DF;
            &::placeholder{font-size: 14px;color: #a7a7a7;}
            &:-ms-input-placeholder{font-size: 14px;color: #a7a7a7;}
            &::-webkit-input-placeholder{font-size: 14px;color: #a7a7a7;}    /* 使用webkit内核的浏览器 */
            &:-moz-placeholder{font-size: 14px;color: #a7a7a7;}                  /* Firefox版本4-18 */
            &::-moz-placeholder{font-size: 14px;color: #a7a7a7;} /* Firefox版本19+ */
          }
        }
      }
      .paper{
          width:314px;
          height: 200px;
          border:1px dashed #dfe0df;
          text-align: center;
          position:relative;
        .papercon{
          position:absolute;
          top:50%;
          left:50%;
          transform: translate(-50%,-50%);
          p{
            font-size:12px;
            color: #bdbdbd;
            width:300px;
          }
        }
      }
    }
    .conone{
      padding-bottom:15px;
    }
    .contwo{
      height: 310px;
      p{
        span{
          font-size: 14px!important;
        }
        select{
          width:130px;
          height: 40px;
          padding-left: 5px;
          margin-left: 24px;
          border-radius:5px; 
          color:#a7a7a7;
          border:1px solid #dfe0df;
        }
        textarea{
          width:87%;
          height: 100px;
          padding-left: 10px;
          padding-top:10px;
          float:right;
          border:1px solid #dfe0df;
          border-radius:5px;
          resize: none;
          font-family: "微软雅黑";
          &::placeholder{font-size: 14px;color: #a7a7a7;}
          &:-ms-input-placeholder{font-size: 14px;color: #a7a7a7;}
          &::-webkit-input-placeholder{font-size: 14px;color: #a7a7a7;}    /* 使用webkit内核的浏览器 */
          &:-moz-placeholder{font-size: 14px;color: #a7a7a7;}                  /* Firefox版本4-18 */
          &::-moz-placeholder{font-size: 14px;color: #a7a7a7;} /* Firefox版本19+ */
      }
    }
    
  }
  .btn{
    text-align: center;
    margin:50px auto 80px auto;
    input[type="submit"]{
      width:160px;
      height: 50px;
      cursor: pointer;
      font-size: 18px;
      color: #5e5e5e;
      border:1px solid #c6c6c6;
      border-radius:5px;
      background-color: #fff;
      margin:0 5px;
      
    }
    .active{
        border:1px solid #EA6457!important;
        background-color:#EA6457!important;
        color: #fff!important;
        
      }
  }
}
</style>



